import {
  LS_USER_KEY,
  credential_mobil,
  credential_email,
  credential_address, 
  credential_birthday 
} from "../../Const";

const initialState = {
  name: { id: "name", value: "", status: 'false' },
  lastName: { id: "lastName", value: "", status: 'false' },
  [credential_email]: { id: credential_email, value: "", status: 'false' },
  [credential_mobil]: { id: credential_mobil, value: "", status: 'false' },
  [credential_birthday]: { id: credential_birthday, value: "", status: 'false' },
  [credential_address]: {
    id: credential_address,
    value: {
      address_line_1: "",
      address_line_2: "",
      postal_code: "",
      city: "",
      country: "",
    },
    status: 'false',
  },
  dynamicFields: [],
};

const UserReducer = (state = initialState, action) => {
  let newState;

  //const field = state[action.payload.id] || {}
  const { payload } = action;

  switch (action.type) {
    case "update":
      console.log(state);
      console.log(action.payload);

      const field = { ...state[payload.id], ...payload };
      newState = { ...state, [payload.id]: field };
      localStorage.setItem(LS_USER_KEY, JSON.stringify(newState));
      return newState;


    case 'add-dynamic-field':
      console.log(state);
      console.log(action.payload);
      newState = {...state, dynamicFields: state.dynamicFields.concat(payload) }
      localStorage.setItem(LS_USER_KEY, JSON.stringify(newState));
      return newState

    case "update-dynamic-field":
      console.log(state);
      console.log(action.payload);
      newState = {
        ...state,
        dynamicFields: state.dynamicFields.map((field) => {
          if (field.id === payload.id) {
            return { ...field, ...payload };
          }
          return field;
        }),
      };
      localStorage.setItem(LS_USER_KEY, JSON.stringify(newState));
      return newState;

    case "load_store_data":
      return action.payload;
    default:
      return state;
  }
};

export default UserReducer;
